<script>
  export let page = "phone";
  export let changePage = () => {};
  $: style = page;
</script>
<style>
.bar{
  max-height: 15vh !important;
}
</style>

<div
  class="bar border-t-2 fixed bottom-0 right-0 left-0 p-2 bg-white flex z-50
  justify-around text-center text-gray-600">
  <div class={style === 'phone' ? 'text-green-400' : ''}>
    <i class="fa fa-phone text-xl transform rotate-90" />
    <button
      on:click={() => changePage('phone')}
      class={'font-semibold text-sm block'}>
      Phone
    </button>
  </div>
  <div class={style === 'contacts' ? 'text-green-400' : ''}>
    <i class={'fa fa-user text-xl'} />
    <button
      on:click={() => changePage('contacts')}
      class={'font-semibold text-sm block'}>
      Contacts
    </button>
  </div>
  <div class={style === 'favourites' ? 'text-green-400' : ''}>
    <i class={'fa fa-star text-xl'} />
    <button
      on:click={() => changePage('favourites')}
      class={'font-semibold text-sm block'}>
      Favourites
    </button>
  </div>
</div>
